* {
  box-sizing: border-box; }

body {
  background: whitesmoke;
  padding: 2em 0; }

label[for="new-item"] {
  display: block; }

.balanced {
  text-align: center; }

#task-list {
  margin-top: 0;
  margin-bottom: 1rem; }

#todo {
  margin: 0 auto;
  text-align: left;
  max-width: 24em;
  background: white;
  padding: 1em;
  border: 1px solid #e6e6e6; }

.new-item__bar {
  display: flex;
  margin: 1em 0; }

#new-item {
  flex: 1 0 auto;
  margin-right: 1em; }

#todo footer {
  text-align: center; }

.task-bar {
  margin-top: 1em; }

a.subtle {
  color: currentColor;
  text-decoration: none; }
